.desktop-shadow {
  .mobile-page-style ();
}

// tablet-version

@media (min-width: @tablet-width) {
  .desktop-shadow {
    .tablet-page-style();
  }
}

  // desktop-version

@media (min-width: @desktop-width) {
.desktop-shadow {
  .desktop-page-style ();
  box-shadow: 0 -10px 17px 10px rgba(0, 0, 0, 0.1);
 }
}
